import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmPlaybackControl,
  VmControl,
  VmIcon,
  VmTooltip 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-playback-control', VmPlaybackControl);
define('vm-control', VmControl);
define('vm-icon', VmIcon);
define('vm-tooltip', VmTooltip);
  

export interface PlaybackControlProps extends JSX.VmPlaybackControl, HTMLAttributes<HTMLVmPlaybackControlElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmPlaybackControlElement, PlaybackControlProps>(
  'vm-playback-control',
  new Set(['playIcon','pauseIcon','icons','tooltipPosition','tooltipDirection','hideTooltip','keys','paused','i18n']),
);
  